import { Component, OnInit, HostBinding, HostListener, ChangeDetectionStrategy, ChangeDetectorRef  } from '@angular/core';

import { MdDialog } from '@angular/material';
import { NewProjectComponent } from '../new-project/new-project.component';
import { InviteComponent } from '../invite/invite.component';
import { ConfirmDialogComponent } from '../../shared/confirm-dialog/confirm-dialog.component';
import { slideToRight} from '../../anims/router.anim';

@Component({
  selector: 'app-project-list',
  templateUrl: './project-list.component.html',
  styleUrls: ['./project-list.component.scss'],
  animations: [
    slideToRight
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProjectListComponent implements OnInit {
  @HostBinding('@routeAnim') state;
  projects= [
    {
      "id": 0,
      "name": "企业协作平台",
      "desc":"这是一个企业内部项目",
      "coverImg":"assets/img/covers/0.jpg"
    },
    {
      "id":1,
      "name": "自动化测试项目",
      "desc":"这是一个企业内部项目",
      "coverImg":"assets/img/covers/1.jpg"
    }
  ];

  constructor(private dialog: MdDialog, private cd: ChangeDetectorRef) { }

  ngOnInit() {

  }
  openNewProjectDialog (){
    const dialogRef = this.dialog.open(NewProjectComponent, {data: {title: '新建项目'}});
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      this.projects = [...this.projects,
      {id: 3, name: '又一个新项目', desc: '这是一个新项目', coverImg: 'assets/img/covers/8.jpg'}
      ];
    this.cd.markForCheck();
    });
  }

  launchInviteDialog() {
     const dialogRef = this.dialog.open(InviteComponent);
    dialogRef.afterClosed().subscribe(result => console.log(result));
  }

  launchEditDialog() {
    const dialogRef = this.dialog.open(NewProjectComponent,  {data: {title: '编辑项目'}});

  }

  launchConfirmDialog() {
     const dialogRef = this.dialog.open(ConfirmDialogComponent, {data: {title: '删除项目', content: '您确认删除该项目吗？'}});
    dialogRef.afterClosed().subscribe(result => console.log(result));
    this.cd.markForCheck();
  }
}



