import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MdToolbarModule,
         MdIconModule, 
         MdButtonModule,
         MdInputModule,
         MdCardModule,
         MdListModule,
         MdSlideToggleModule,
         MdGridListModule,
         MdDialogModule,
         MdAutocompleteModule,
         MdMenuModule,
         MdCheckboxModule,
         MdTooltipModule,
         MdRadioModule,
         MdDatepickerModule,
         MdNativeDateModule,
         MdSelectModule,
         MdSidenavModule
       } from '@angular/material';
import { ConfirmDialogComponent } from './confirm-dialog/confirm-dialog.component';
import { DirectiveModule } from '../directive/directive.module';
import { ImageListSelectComponent } from './image-list-select/image-list-select.component'
@NgModule({
  imports: [
    CommonModule,
    MdToolbarModule,
    MdIconModule,
    MdButtonModule,
    MdInputModule,
    MdCardModule,
    MdListModule,
    MdSlideToggleModule,
    MdGridListModule,
    MdDialogModule,
    MdAutocompleteModule,
    MdMenuModule,
    MdCheckboxModule,
    MdTooltipModule,
    MdRadioModule,
    MdDatepickerModule,
    MdNativeDateModule,
    MdSelectModule,
    MdSidenavModule,
    DirectiveModule,
    FormsModule, 
    ReactiveFormsModule
  ],
  exports: [
    CommonModule,
    MdToolbarModule,
    MdIconModule,
    MdButtonModule,
    MdInputModule,
    MdCardModule,
    MdListModule,
    MdSlideToggleModule,
    MdGridListModule,
    MdAutocompleteModule,
    MdMenuModule,
    MdCheckboxModule,
    MdTooltipModule,
    MdRadioModule,
    MdDatepickerModule,
    MdNativeDateModule,
    MdSelectModule,
    MdSidenavModule,
    DirectiveModule,
    FormsModule, 
    ReactiveFormsModule,
    ImageListSelectComponent
  ],
  entryComponents: [ConfirmDialogComponent],
  declarations: [ConfirmDialogComponent, ImageListSelectComponent]
})
export class SharedModule { }



