import { Component } from '@angular/core';
import { OverlayContainer } from '@angular/material';
import { trigger, state, transition, style, animate, keyframes } from '@angular/animations';
//import { ROUTER_PROVIDERS,RouteConfig, ROUTER_DIRECTIVES,APP_BASE_HREF,LocationStrategy, } from '@angular/router';
//import {ROUTER_PROVIDERS,RouteConfig, ROUTER_DIRECTIVES,APP_BASE_HREF,LocationStrategy,RouteParams,ROUTER_BINDINGS} from '@angular/router';
import { APP_BASE_HREF } from '@angular/common';
//import { ROUTER_PROVIDERS} from '@angular/router';
//import { RouteConfig, ROUTER_DIRECTIVES } from '@angular/router';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  darkTheme = false;

  constructor(private oc: OverlayContainer){
  }

  switchTheme(dark) {
    this.darkTheme = dark;
    this.oc.themeClass = dark? 'myapp-dark-theme' : null;
  }
  
}





