import { Component, OnInit, HostBinding, HostListener, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { MdDialog } from '@angular/material';
import { NewTaskComponent } from '../new-task/new-task.component';
import { CopyTaskComponent } from '../copy-task/copy-task.component';
import { ConfirmDialogComponent } from '../../shared/confirm-dialog/confirm-dialog.component';
import { NewTaskListComponent } from '../new-task-list/new-task-list.component';
import { slideToRight } from '../../anims/router.anim';
import { QuickTaskComponent } from '../quick-task/quick-task.component';

@Component({
  selector: 'app-task-home',
  templateUrl: './task-home.component.html',
  styleUrls: ['./task-home.component.scss'],
  animations: [ slideToRight ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TaskHomeComponent implements OnInit {
  @HostBinding('@routeAnim') state;
  lists = [
    {
      id: 1,
      name: '待办',
      tasks: [
        {
          id: 1,
          desc: '任务一：去星巴克买咖啡',
          completed: true,
          priority: 3,
          owner: {
            id: 1,
            name: '张三',
            avatar: 'avatars:svg-11'
          },
          dueDate: new Date(),
          reminder: new Date(),
        },
         {
          id: 2,
          desc: '任务一：去肯德基买汉堡',
          completed: false,
          priority: 2,
          owner: {
            id: 1,
            name: '李四',
            avatar: 'avatars:svg-2'
          },
          dueDate: new Date(),
        },
      ]
    },
     {
      id: 2,
      name: '待办2',
      tasks: [
        {
          id: 1,
          desc: '任务一：去发宣传单',
          completed: false,
          priority: 3,
          owner: {
            id: 1,
            name: '王五',
            avatar: 'avatars:svg-7'
          },
          dueDate: new Date(),
          reminder: new Date()
        },
         {
          id: 2,
          desc: '任务一：做海报设计',
          completed: false,
          priority: 1,
          owner: {
            id: 1,
            name: '赵六',
            avatar: 'avatars:svg-3'
          },
          dueDate: new Date(),
        },
      ]
    }
  ];
  constructor(private dialog: MdDialog, private cd: ChangeDetectorRef) { }

  ngOnInit() {
  }

  launchNewTaskDialog(){
    this.dialog.open(NewTaskComponent, {data: {title: '新建任务'}});
  }

  launchCopyTaskDialog(){
    const dialogRef=this.dialog.open(CopyTaskComponent, {data: {lists: this.lists}});
  }
  launchUpdateTaskDialog(task){
    const dialogRef=this.dialog.open(NewTaskComponent, {data: {title:"修改任务", task:task}});
  }
  launchConfirmDialog(){
    const dialogRef=this.dialog.open(ConfirmDialogComponent, {data: {title:"修改任务", content:"您确认删除本列表吗？"}});
  //dialogRef.afterClosed.subscribe(result=>console.log(result));
    dialogRef.afterClosed().subscribe(result => console.log(result));
  }
  launchEditListDialog(){
    const dialogRef = this.dialog.open(NewTaskListComponent, {data:{title:'修改列表名称'}});
    dialogRef.afterClosed().subscribe(result => console.log(result));
  }
  launchNewListDialog(){
     const dialogRef = this.dialog.open(NewTaskListComponent, {data:{title:'新增列表'}});
    dialogRef.afterClosed().subscribe(result => console.log(result));
  }

  handleQuickTask(desc: string) {
    console.log(desc);
  }
}



